.navbar_container {
  background-color: white;
  width: 100%;
  top: 0;
  left: 0;
  position: fixed;
  padding: 0;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2);
}

.navbar_content {
  margin-left: 160px;
  margin-right: 160px;
  margin-top: 25px;
  margin-bottom: 25px;
}

.navbar_items {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.right_items {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.right_items div {
  margin: 0px;
}

.left_items {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.link {
  color: #213343;
  text-decoration: none;
}

.item {
  display: flex;
  font-size: 18pt;
  margin-right: 40px;
}

.item:hover {
  cursor: pointer;
  font-weight: bold;
  border-bottom: 1px solid #213343;
}

.img_item {
  margin-right: 40px;
}

.img_item:hover {
  border-bottom: none;
}

#github_logo {
  margin-left: 10px;
}

#thoth_logo {
  margin: 0px;
}
