.footer_container {
  background-color: #213343;
  width: 100%;
  box-shadow: 0px -1px 2px rgba(0, 0, 0, 0.2);
  clear: both;
  position: relative;
  height: 90px;
  margin-top: -90px;
}

.footer_content {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px 10px;
}

.footer {
  color: #8fa4b5;
  font-size: 16px;
}
